import React, { lazy, useEffect, useState } from 'react'
import { AnimationWrapper } from 'react-hover-animation'
import store from '../../../redux/store'
import {ReactComponent as Beras} from '../grain.svg'
import {ReactComponent as Garam} from '../salt.svg'
import {
  CBadge,
  CButton,
  CModal,
  CAlert,
  CButtonGroup,
  CLink,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout,
  CDataTable,
  CCollapse,
  CModalHeader,
  CModalBody
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {fieldviavalen} from './source.js'
import { useSelector } from 'react-redux'

const ViaValenDataDemand = (props) => {
  const komoditas = useSelector(state => state.komoditas.komoditas) 
  const [tambah, setTambah] = useState("")
  const [data, setData] = useState([
    {
      id:1,
      noaju:"4000077EEF8F61512202100001",
      rincianpenggunaan:"Rumah Tangga",
      bulan:"Februari",
      jenis:"Garam",
      periode:"2020",
      status:"Draft"
    },
    {
      id:2,
      noaju:"4000088AAF5461512202000002",
      rincianpenggunaan:"Rumah Tangga",
      bulan:"Januari",
      jenis:"Beras",      
      periode:"2020",
      status:"Terkirim"
    }, 
    {
      id:3,
      noaju:"400007799F8F61513201900001",
      rincianpenggunaan:"Tambak Garam",
      bulan:"Januari",
      jenis:"Garam",
      periode:"2020",
      status:"Terkirim"
    },   
    {
      id:4,
      noaju:"4000099OOF8K61512201800001",
      rincianpenggunaan:"Komersial",
      bulan:"Januari",
      jenis:"Garam",      
      periode:"2020",
      status:"Terkirim"
    },      
  ])
  const handleTambah = () => {
    setTambah("ya")
  }
  const [details,setDetails] = useState([])
  const toggleDetails = (index) =>{
    const posisi = details.indexOf(index)
    let newDetails = details.slice()
    if(posisi !== -1){
      newDetails.splice(posisi, 1)
    } else {
      newDetails = [...details, index]
    }
    setDetails(newDetails)
  }
  useEffect(() => {
    if(tambah == "ya"){
        props.history.push('/kl/form')
    }
  }); 
  const [modalrincianpenggunaan, setModalrincianpenggunaan] = useState(false)
  const [modalkomoditas, setModalkomoditas] = useState(false)
  const handleKomoditas = (e) =>{
    var value = e.target.id
    setModalkomoditas(false)
    setModalrincianpenggunaan(true)
    store.dispatch({ type: 'komoditas/changeKomoditas', payload: value })
  }
  return (
    <>
     {/* <CAlert color="warning" closeButton>
        <h6>SIMULASI USER K/L</h6>
      </CAlert>      */}
    <CModal 
      onClose={()=>setModalkomoditas(false)}
      show={modalkomoditas}>
        <CModalHeader>
          <h4>Pilih Komoditas</h4>
        </CModalHeader>
        <CModalBody>
          <CRow>
            <CCol md="6">
              <AnimationWrapper>
              <CCard id="beras" style={{color:"black"}} color="success">
                <CCardBody>
                  <div id="beras" className="d-flex justify-content-center">
                    <Beras/>
                  {/* <CIcon size={'3xl'} name={'cilBasket'} />                      */}
                  </div>  
                  <div className="pt-2 d-flex justify-content-center">
                  <h3>Beras</h3>                     
                  </div>                                
                </CCardBody>
              </CCard> 
              <CLink id="beras" onClick={(e)=>handleKomoditas(e)} class="stretched-link"></CLink>               
              </AnimationWrapper>
            </CCol>
            <CCol md="6">
              <AnimationWrapper>
              <CCard id="garam" style={{color:"black"}} color="warning">
                <CCardBody>
                <div id="garam" className="d-flex justify-content-center">
                  <Garam/>
                  {/* <CIcon size={'3xl'} name={'cilCart'} />                      */}
                  </div>  
                  <div className="pt-2 d-flex justify-content-center">
                  <h3>Garam</h3>                     
                  </div>                                     
                </CCardBody>
              </CCard>   
              <CLink id="garam" onClick={(e)=>handleKomoditas(e)} class="stretched-link"></CLink>                           
              </AnimationWrapper>
            </CCol>            
          </CRow>
        </CModalBody>
      </CModal>      
    <CModal 
    show={modalrincianpenggunaan}
    onClose={()=>setModalrincianpenggunaan(false)}
    >
      <CModalHeader closeButton>
        <h4>Pilih Form</h4>
      </CModalHeader>
      <CModalBody>
      <AnimationWrapper>
      <CCard color="success" className="text-white text-center">
            <CCardBody>
            <CLink to='/kl/formdemand'class="stretched-link"></CLink>
              <blockquote className="card-bodyquote">
                <h5>Rincian Rencana Kebutuhan Rumah Tangga atau Komersial</h5>
              </blockquote>
            </CCardBody>
          </CCard>
      </AnimationWrapper>
      {/* <AnimationWrapper>
      <CCard color="info" className="text-white text-center">
            <CCardBody>
            <CLink to='/kl/formdemand'class="stretched-link"></CLink>
              <blockquote className="card-bodyquote">
                <h4>Rincian Rencana Kebutuhan Komersial</h4>
              </blockquote>
            </CCardBody>
          </CCard> 
      </AnimationWrapper> */}
      {
        komoditas == "garam" ?
        <>
          <AnimationWrapper>
          <CCard color="primary" className="text-white text-center">
                <CCardBody>
                <CLink to='/kl/formsupply'class="stretched-link"></CLink>
                  <blockquote className="card-bodyquote">
                    <h5>Rincian Rencana Produksi Tambak Garam</h5>
                  </blockquote>
                </CCardBody>
              </CCard>  
          </AnimationWrapper>  
        </>
        :
        <>
        </>                
      }                             
      </CModalBody>
    </CModal>
    <CCard>
      <CCardHeader>
        <h4>Histori Data Demand-Supply Non Industri</h4>
      </CCardHeader>
      <CCardBody>
        <CDataTable
          fields={fieldviavalen}
          items={data}
          addTableClasses="josss"
          overTableSlot={<div className="d-flex justify-content-end pb-2"><CButton onClick={()=>setModalkomoditas(true)} color="info"> Tambah + </CButton></div>}   
          scopedSlots = {{
            'action':
              (item)=>{
                var disable = false
                if(item.status !== "Draft"){
                  disable = true
                }
                return(
                  <td>
                      <CButton size="sm" color="info" disabled={disable}>Edit</CButton>{' '}
                      <CButton size="sm" color="success" >Lihat</CButton>{' '}
                      <CButton size="sm" color="danger" disabled={disable}>Hapus</CButton>
                  </td>                  
                )
              },
              'no':
              (item)=>(
                <td>
                    {item.id}
                </td>
              ),
              'status':
              (item, index)=>(
                <td>
                  <CButton onClick={()=>{toggleDetails(index)}}>
                  {details.includes(index) ? 
                  <CIcon size={'sm'} name={'cilArrowCircleBottom'} /> 
                  :
                  <CIcon size={'sm'} name={'cilArrowCircleRight'} />
                  }  
                  </CButton>{' '}{item.status}
                </td>
              ),
              'details':
              (item, index)=>(
                <CCollapse show={details.includes(index)}>
                  <table>
                  {item.status == "Draft"
                  ?
                  <tr>
                    <td style={{width:750}}>
                    </td>
                    <td style={{width:200}}>
                      - Draft                  
                    </td>
                    <td style={{width:400}}>
                    04/03/2021 20:48:29
                    </td>                    
                  </tr>   
                  :
                  <>
                  <tr>
                    <td style={{width:750}}>
                    </td>
                    <td style={{width:200}}>
                      - Draft                  
                    </td>
                    <td style={{width:400}}>
                    04/03/2021 20:48:29
                    </td>                    
                  </tr>
                  <tr>
                    <td style={{width:750}}>
                    </td>
                    <td style={{width:200}}>
                      - Terkirim                  
                    </td>
                    <td style={{width:400}}>
                    04/03/2021 20:48:29
                    </td>                    
                  </tr>                                       
                  </>                                 
                  }
                  </table>
                </CCollapse>
              )                                         
          }}
        />    
      </CCardBody>
    </CCard>
    </>
  )
}

export default ViaValenDataDemand
